  margin: 0;
  padding: 0;
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

.input {
  color: black;
  font-family: inherit;
  font-weight: 300;
  border: 1px solid rgb(229, 231, 235);
  border-radius: 5px;
  outline: none;
  width: 250px;
  padding: 6px 10px;
  font-size: 17px;
  cursor: pointer;
}

.input::placeholder,
input[type=date]:invalid {
  -webkit-text-fill-color: gray;
}

input[type="date"] {
  color: black;
}

input[type="checkbox"] {
  width: 40px;
  height: 40px;
}

.label {
  font-size: 18px;
  font-weight: 300;
}

.form-sub-container {
  display: flex;
  flex-direction: column;
}

.input:focus {
  border: 1px solid rgb(16, 107, 255);
  transform: scale(1);
  transition: .8s ease;
}

body {
  display: grid;
  grid-template-columns: 300px 1fr;
  height: 100vh;
}

button {
  cursor: pointer;
}

:root {
  font-family: 'Inter', sans-serif;
}

.sidebar {
  display: flex;
  flex-direction: column;
  padding: 20px 25px;
  background-color: rgb(241, 241, 241);
}

.tabs {
  display: flex;
  flex-direction: column;
  gap: 15px;
  padding-bottom: 20px;
  border-bottom: 1px solid rgb(200, 200, 200);
}

.tab-cont,
.user-list {
  display: flex;
  align-items: center;
  gap: 8px;
  width: max-content;
  cursor: pointer;
  transform: scale(1);
  transition: .4s ease;
}

.tab-cont:hover,
.user-list:hover {
  transform: scale(1.1);
  transition: .4s ease;
}

.tab-btn {
  font-family: inherit;
  font-weight: 300;
  font-size: 16px;
  text-align: start;
}

.tab-icon {
  width: 29px;
  height: auto;
}

.form {
  display: flex;
  flex-direction: column;
  gap: 22px;
}


.form-sub-container {
  display: grid; 
  justify-content: start;
  align-items: center;
  justify-items: start;
  row-gap: 8px;
}

.label {
  grid-row: 1;
}

.input {
  grid-row: 2;
}

#create-task-btn {
  align-self: center;
  width: 100px;
  margin-top: 10px;
  font-family: inherit;
  font-size: 17px;
  font-weight: 300;
  border: 1px solid rgb(155, 155, 155);
  border-radius: 5px;
  background-color: #f2f2f2;
  padding: 3px 0;
  transform: scale(1);
  transition: .5s ease;
}

#create-task-btn:hover {
  background-color: rgb(236, 236, 236);
  transform: scale(1.1);
  transition: .5s ease;
}

.content {
  display: flex;
  flex-direction: column;
  gap: 18px;
  padding: 60px 60px;
  background-color: rgb(255, 255, 255);
}

.task-cont {
  animation-name: slidein;
  animation-duration: .5s;
  display: grid;
  grid-template-columns: max-content max-content max-content max-content;
  align-items: center;
  gap: 12px;
  font-size: 18px;
  font-weight: 300;
  border-radius: 8px;
  background-color: rgb(255, 255, 255);
}

@keyframes slidein {
  from {
    transform: scale(0);
    opacity: 0;
  }
  to {
    transform: scale(1);
    opacity: 1;
  }
}

.task-cont.active {
  transform: scale(0);
  transition: 0.5s ease;
}

.date-cont {
  padding: 3px 7px;
  font-size: 15px;
  font-weight: 600;
  border-radius: 5px;
  color: rgb(105, 105, 105);
  background-color: rgb(232, 232, 232);
}

.complete-cont {
  width: 20px;
  height: 20px;
  border: 2px solid rgb(182, 182, 182);
  border-radius: 6px;
}

.complete-cont.active {
  background-color: rgb(218, 218, 218);
}

.priority-cont {
  font-size: 15px;
  font-weight: 400;
  padding: 1px 10px;
  border: 2px solid rgb(187, 187, 187);
  border-radius: 12px;
  color: rgb(185, 185, 185);
}

.tab-header {
  margin-bottom: 22px;
  font-size: 30px;
  font-weight: 700;
}

.project-name {
  padding-bottom: 10px;
  margin-bottom: 12px;
  font-size: 18px;
  font-weight: 700;
  border-bottom: 2px solid rgb(226, 226, 226);
}

.new-task-window {
  position: fixed;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, .3);
  display: flex;
  justify-content: center;
  align-items: center;
  opacity: 0;
  pointer-events: none;
}

.new-task-window.active {
  opacity: 1;
  pointer-events: auto;
  transition: .4s ease;
}

.new-task-window .new-task-box {
  width: 350px;
  background-color: #f2f2f2;
  border-radius: 6px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
  cursor: pointer;
  padding: 30px;
  transform: scale(0);
}

#closing-btn {
  position: fixed;
  right: 3px;
  top: 3px;
  width: 36px;
  height: auto;
}


.new-task-window.active .new-task-box {
  transform: scale(1);
  transition: .4s ease;
  transition-delay: .25s;
}

#priority {
  width: 22px;
  height: 22px;
}

.new-task-header {
  text-align: center;
  font-size: 20px;
  font-weight: 600;
  margin: 10px 0 18px 0;
}

.tab-btn {
  border: none;
}

#new-list {
  font-family: inherit;
  font-size: inherit;
  font-weight: 300;
  outline: none;
  background: none;
  border: none;
  width: 100px;
}


.new-list-cont {
  display: flex;
  align-items: center;
  gap: 8px;
  width: max-content;
  cursor: pointer;
  transform: scale(1);
  transition: .4s ease;
  margin-top: 20px;
}

.new-list-cont:hover {
  transform: scale(1.1);
  transition: .4s ease;
}

#new-list::placeholder {
  color: black;
}

#new-list:focus::placeholder {
  transition: .4s ease;
  color: transparent;
}

.user-tabs {
  display: flex;
  flex-direction: column;
  gap: 15px;
  padding-bottom: 20px;
  margin-top: 20px;
  border-bottom: 1px solid rgb(200, 200, 200);
}

.user-list {
  display: flex;
  align-items: center;
  gap: 8px;
  width: max-content;
  cursor: pointer;
  transform: scale(1);
  transition: .4s ease;
  font-weight: 300;
}

.user-list.active {
  animation-name: slidein;
  animation-duration: .5s;
}

.user-tab-icon {
  content: url(${___CSS_LOADER_URL_REPLACEMENT_0___});
  width: 29px;
  height: auto;
}

.list-container {
  display: flex;
  flex-direction: column;
  gap: 18px;
  width: 500px;
  margin-bottom: 20px;
}

.list-header {
  font-size: 20px;
  font-weight: 500;
}

.task-of-list {
  padding-left: 12px;
}

.tab-header,
.list-header {
  animation-name: slidein;
  animation-duration: .5s;
}

.list-container.active {
  transform: scale(0);
  transition: 0.5s ease;
}

#no-tasks-info {
  animation: slidein;
  animation-duration: .5s;
  font-size: 34px;
  font-weight: 300;
