import TodoList from "./logic";
import { content, listInput } from "..";
import { format } from "date-fns";
import userTab from "../pages/user-tab";

export default class DOM {
  static addTask(task) {
    const taskCont = document.createElement("div");
    const completeCont = document.createElement("div");
    const titleCont = document.createElement("div");
    const dateCont = document.createElement("div");
    const priorityCont = document.createElement("div");

    if (task.list !== "none" && content.id !== "user-tab")
      taskCont.classList.add("task-of-list");

    taskCont.classList.add("task-cont");
    completeCont.classList.add("complete-cont");
    titleCont.classList.add("title-cont");
    dateCont.classList.add("date-cont");
    priorityCont.classList.add("priority-cont");

    titleCont.textContent = task.title;
    dateCont.textContent = task.date;
    priorityCont.textContent = "Important";

    completeCont.onclick = () => {
      if (
        task.list !== "none" &&
        taskCont.parentElement.childElementCount === 2
      )
        taskCont.parentElement.classList.add("active");
      completeCont.classList.add("active");
      taskCont.classList.add("active");
      setTimeout(function () {
        if (
          task.list !== "none" &&
          taskCont.parentElement.childElementCount === 2 &&
          content.id !== "user-tab"
        ) {
          content.removeChild(taskCont.parentElement);
        } else if (task.list !== "none" && content.id !== "user-tab") {
          taskCont.parentElement.removeChild(taskCont);
        } else {
          content.removeChild(taskCont);
        }
        if (content.childElementCount === 1) {
          const noTasksinfo = document.createElement("div");
          noTasksinfo.id = "no-tasks-info";
          noTasksinfo.textContent = "There are no tasks to display in this tab";
          content.appendChild(noTasksinfo);
        }
      }, 200);

      TodoList.remove(task.index);
    };

    taskCont.appendChild(completeCont);
    if (task.date !== "")
      (dateCont.textContent = format(task.date, "ccc dd MMM")),
        taskCont.appendChild(dateCont);
    taskCont.appendChild(titleCont);
    if (task.priority === true) taskCont.appendChild(priorityCont);

    return taskCont;
  }

  static addTabList(list) {
    let userTabs = JSON.parse(localStorage.getItem("userTabs"));
    if (userTabs === null) userTabs = [];

    const userListCont = document.createElement("div");
    const userListIcon = document.createElement("img");
    const userListName = document.createElement("div");
    const option = document.createElement("option");
    userListCont.classList.add("user-list", "active");
    userListIcon.classList.add("user-tab-icon");
    userListName.textContent = list;
    option.textContent = list;
    option.value = list;
    userListCont.append(userListIcon, userListName);
    listInput.appendChild(option);

    userListCont.addEventListener("click", () => {
      userTab.initTab(list);
    });

    return userListCont;
  }

  static addContentList(list) {
    const listCont = document.createElement("div");
    const listHeader = document.createElement("div");
    listCont.classList.add("list-container");
    listHeader.classList.add("list-header");
    listHeader.textContent = list;
    listCont.appendChild(listHeader);
    return listCont;
  }
}
